<template>
    <div>
    <loading :active.sync="isLoading"></loading>
      <table class="table mt-4">
          <thead class="thead-light">
              <tr>
                  <th width="100">購買時間</th>
                  <th width="300">Email</th>
                  <th>購買款項</th>
                  <th width="100">應付金額</th>
                  <th width="100" class="text-center">是否付款</th>
              </tr>
          </thead>
          <tbody>
              <tr v-for="(item) in orders" :key="item.id">
                <td>{{item.create_at | date}}</td>
                <td><span v-text="item.user.email" v-if="item.user"></span></td>
                <td>
                    <ul class="list-unstyled">
                        <li v-for="(product, i) in item.products" :key="i">
                            {{ product.product.title }} 數量：{{ product.qty }}
                            {{ product.product.unit }}
                        </li>
                    </ul>
                  </td>
                  <td class="text-right">{{ item.total | currency }}</td>
                  <td class="text-center">
                      <span v-if="item.is_paid" class="text-success">已付款</span>
                      <span v-else class="text-secondary">尚未付款</span>
                  </td>
              </tr>
          </tbody>
      </table>
       <nav aria-label="Page navigation example">
        <ul class="pagination d-flex justify-content-center pt-4">
          <li class="page-item" :class="{'disabled': !pagination.has_pre}">
            <a class="page-link" href="#" aria-label="Previous" @click.prevent="getOrder(pagination.current_page - 1)">
              <span aria-hidden="true">&laquo;</span>
              <span class="sr-only">Previous</span>
            </a>
          </li>
          <li class="page-item" v-for="page in pagination.total_pages" :key="page" :class="{'active':pagination.current_page === page}">
            <a class="page-link" href="#" @click.prevent="getOrder(page)">{{ page }}</a>
          </li>
          <li class="page-item" :class="{'disabled': !pagination.has_next}">
            <a class="page-link" href="#" aria-label="Next" @click.prevent="getOrder(pagination.current_page + 1)">
              <span aria-hidden="true">&raquo;</span>
              <span class="sr-only">Next</span>
            </a>
          </li>
        </ul>
        </nav>
  </div>
</template>

<script>
export default {
  data () {
    return {
      orders: [{
        user: {}
      }],
      isLoading: false,
      pagination: {}
    }
  },
  methods: {
    getOrder (page = 1) {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/orders?page=${page}`
      const vm = this
      // console.log(api)
      vm.isLoading = true
      this.$http.get(api).then(response => {
        console.log(response.data)
        vm.isLoading = false
        vm.orders = response.data.orders
        vm.pagination = response.data.pagination
        console.log(vm.orders)
      })
    }
  },
  created () {
    this.getOrder()
  }
}
</script>



