<template>
  <div class="py-4">
    <loading :active.sync="isLoading"></loading>
    <a href="#" class="btn btn-outline-primary" @click.prevent="toshopping">
      <i class="fas fa-undo-alt mr-2"></i>繼續購物
    </a>
    <div class="row  d-flex justify-content-center">
      <div class="mt-5 col-8">
        <table class="table">
          <thead>
            <tr>
              <th scope="col" width="80"></th>
              <th scope="col">品名</th>
              <th scope="col" width="120">數量</th>
              <th scope="col" class="text-right">單價</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item,key) in carts" :key="key">
              <th scope="row" class="text-center">
                  <a href="#" class="btn btn-outline-danger btn-sm" @click.prevent="removeCartItem(item.id)"><i class="fas fa-trash-alt"></i></a>
              </th>
              <td>
                  {{item.product.title}}
                  <div class="text-success" v-if="item.coupon">已套用優惠券:{{item.coupon.title}}</div>
              </td>
              <td>{{item.qty}}</td>
              <td class="text-right">{{item.total | currency}}</td>
            </tr>
          </tbody>
          <tfoot>
              <tr>
                <td scope="col" colspan="3" class="text-right">總額</td>
                <td scope="col" class="text-right">{{cart.total | currency}}</td>
              </tr>
              <tr v-if="cart.final_total !== cart.total">
                  <td scope="col" colspan="3" class="text-right text-success">優惠總額</td>
                  <td scope="col" class="text-right">{{cart.final_total | currency}}</td>
              </tr>
          </tfoot>
        </table>
        <div class="input-group mb-4">
            <input type="text" class="form-control" placeholder="請輸入優惠碼" aria-label="Recipient's username" aria-describedby="basic-addon2" v-model="couponCode">
            <div class="input-group-append">
                <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">套用優惠碼</button>
            </div>
        </div>
        <div class="text-right my-3">
            <button class="btn btn-primary" @click.prevent="tocheckout()">結帳去</button>
        </div>
      </div>
    </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      cart: {},
      carts: [],
      isLoading: false,
      couponCode: ''
    }
  },
  methods: {
    getCarts () {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/cart`
      const vm = this
      // console.log(api)
      vm.isLoading = true
      this.$http.get(api).then(response => {
        vm.isLoading = false
        console.log(response.data.data.carts)
        vm.cart = response.data.data
        vm.carts = response.data.data.carts
        console.log(vm.carts)
      })
    },
    removeCartItem (id) {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/cart/${id}`
      const vm = this
      console.log(api)
      vm.isLoading = true
      this.$http.delete(api).then(response => {
        vm.isLoading = false
        console.log(response)
        this.getCarts()
      })
    },
    toshopping () {
      this.$router.push('/customer_order')
    },
    tocheckout () {
      this.$router.push('/customer_checkout')
    },
    addCouponCode () {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/coupon`
      const vm = this
      //   console.log(api)
      vm.isLoading = true
      let code = {
        code: this.couponCode
      }
      //   console.log(code)
      this.$http.post(api, {data: code}).then(response => {
        vm.isLoading = false
        // console.log(response)
        this.getCarts()
      })
    }
  },
  created () {
    this.getCarts()
  }
}
</script>



