<template>
  <div>
    <div class="text-right mt-4">
      <Alert></Alert>
      <!-- 放棄編輯區塊 -->
      <button class="btn btn-link text-danger" data-toggle="modal" data-target="#CancelModal">
        <i class="fas fa-times"></i>
      </button>
    </div>
    <div class="mt-4">
      <h5>新增商品圖片</h5>
      <label for="imgfile">
        <div class="imgbox mt-4 d-flex flex-column justify-content-center" style="position:relative">
          <!-- 如果有已圖片就顯示預覽圖片 -->
          <img :src="product.image" alt v-if="product.image" class="h-100 w-100">
          <div class="hoverEffect h-100 w-100 justify-content-center d-flex align-items-center" :class="{ 'loadingbg': isloading}">
            <!--圖片上傳 loading 效果 -->
              <template v-if="isloading">
                <div class="justify-content-center d-flex align-items-center fa-3x">
                  <i class="fas fa-spinner fa-spin"></i>
                </div>
              </template>
          </div>
          <!-- hover 效果:顯示編輯icon -->
          <div class="hoverEffect h-100 w-100 justify-content-center d-flex align-items-center" v-if="product.image &&!isloading" @mouseover="imgHover=true" @mouseout="imgHover=false" :class="{ 'loadingbg': isloading ,'hoverEffectbg' : imgHover}">
              <template v-if="imgHover && !isloading">
                <!-- 編輯icon -->
                <div class="justify-content-center d-flex align-items-center iconButton fil-input">
                  <label for="editfile">
                    <a href="">
                      <i class="far fa-edit fa-2x">
                        <form action="/api/thisismycourse2/admin/upload" enctype="multipart/form-data" method="post">
                          <input type="file" ref="files" name="" id="editfile" @change="uploadFile" style="outline:none">
                        </form>
                      </i>
                    </a>
                  </label>
                </div>
                <!-- 刪除icon -->
                <div class="justify-content-center d-flex align-items-center iconButton ml-3">
                  <a href="" @click.prevent="deleteimg">
                    <i class="far fa-trash-alt fa-2x"></i>
                  </a>
                </div>
              </template>
          </div>
            <!-- hover 效果:顯示編輯icon -->
           <!--如果還沒有圖片就顯示圖片選擇樣式  -->
          <template v-else>
              <div class="align-self-center">
                <i class="fas fa-plus fa-4x"></i>
              </div>
                <div class="align-self-center pt-2 fil-input">
                   <form action="/api/thisismycourse2/admin/upload" enctype="multipart/form-data" method="post">
                    <input type="file" ref="files" name="" id="imgfile" @change="uploadFile" style="outline:none">點擊新增圖片
                  </form>
                </div>
          </template>
        </div>
      </label>
   </div>
   <!-- 資料表單 -->
    <form class="my-4">
      <div class="form-group">
        <label for="productname">產品名稱</label>
        <input
          type="text"
          class="form-control"
          id="productname"
          placeholder="請輸入產品名稱"
          v-model="product.title"
        >
      </div>
      <div class="form-group">
        <div class="row">
          <div class="col">
            <label for="class">分類</label>
            <input
              type="text"
              class="form-control"
              id="class"
              placeholder="請輸入分類"
              v-model="product.category"
            >
          </div>
          <div class="col">
            <label for="unit">單位</label>
            <input
              type="text"
              class="form-control"
              id="unit"
              placeholder="請輸入單位"
              v-model="product.unit"
            >
          </div>
        </div>
      </div>
      <div class="form-group">
        <div class="row">
          <div class="col">
            <label for="o_price">原價</label>
            <input
              type="text"
              class="form-control"
              id="o_price"
              placeholder="請輸入原價"
              v-model="product.origin_price"
            >
          </div>
          <div class="col">
            <label for="price">售價</label>
            <input
              type="text"
              class="form-control"
              id="price"
              placeholder="請輸入售價"
              v-model="product.price"
            >
          </div>
        </div>
      </div>
      <div class="border-top mt-5">
        <div class="form-group mt-4">
          <label for="Description">產品描述</label>
          <textarea
            class="form-control"
            id="Description"
            rows="3"
            placeholder="請輸入產品描述"
            v-model="product.description"
          ></textarea>
        </div>
        <div class="form-group">
          <label for="infor">說明內容</label>
          <textarea
            class="form-control"
            id="infor"
            rows="3"
            placeholder="產品優惠中"
            v-model="product.content"
          ></textarea>
        </div>
      </div>
      <div class="form-check mb-4">
        <input
          class="form-check-input"
          type="checkbox"
          value
          id="status"
          v-model="product.is_enabled"
          :true-value="1"
          :false-value="0"
        >
        <label class="form-check-label" for="status">是否啟用</label>
      </div>
    </form>
    <div class="text-right py-4">
      <button type="button" class="btn btn-primary mr-3" @click.prevent="updateProduct">{{buttonName}}</button>
      <button type="button" class="btn btn-secondary" data-toggle="modal" data-target="#CancelModal">取消</button>
    </div>
    <!-- Modal -->
    <div
      class="modal fade"
      id="CancelModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header border-bottom-0">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body h4 text-center">捨棄更變?</div>
          <div class="modal-footer border-top-0">
            <button type="button" class="btn btn-light w-50 text-secondary" data-dismiss="modal">取消</button>
            <button
              type="button"
              class="btn btn-light w-50 text-danger"
              data-dismiss="modal"
              @click="cancelnewProduct"
            >放棄</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Alert from '../Alert.vue'
export default {
  components: {
    Alert
  },
  data () {
    return {
      products: [],
      product: {},
      buttonName: '新增',
      imgHover: false,
      isloading: false
    }
  },
  methods: {
    // 放棄目前編輯
    cancelnewProduct () {
      this.$router.push('/admin/products')
    },
    // 新增/更新產品
    updateProduct () {
      const vm = this
      let httpMethod = 'post'
      let api
      if (!this.$router.currentRoute.query.id) { // 新增
        api = `${process.env.APIPATH}/api/${
          process.env.COSTOMPATH
        }/admin/product`
      } else { // 修改
        api = `${process.env.APIPATH}/api/${
          process.env.COSTOMPATH
        }/admin/product/${vm.$router.currentRoute.query.id}`
        httpMethod = 'put'
      }
      console.log(api)
      this.$http[httpMethod](api, { data: vm.product }).then(response => {
        this.$router.push('/admin/products')
        console.log(response.data)
      })
    },
    // 上傳圖片檔案
    uploadFile () {
      this.isloading = true
      // console.log(this)
      const file = this.$refs.files.files[0]
      const vm = this
      if (this.product.image) {
        this.deleteimg()
      }
      const formData = new FormData()
      formData.append('file-to-upload', file)
      const url = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/admin/upload`
      this.$http.post(url, formData, {
        header: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((response) => {
        console.log(response.data)
        vm.isloading = false
        if (response.data.success) {
          // vm.product.image = response.data.imageUrl
          // console.log(vm.product)
          vm.$set(vm.product, 'image', response.data.imageUrl)
        } else {
          vm.$bus.$emit('message:push', response.data.message, 'danger')
        }
      })
    },
    // 刪除目前所選圖片
    deleteimg () {
      this.$set(this.product, 'image', null)
      this.imgHover = false
    }
  },
  created () {
    if (!this.$router.currentRoute.query.id) {
      this.buttonName = '新增'
      return
    }
    this.buttonName = '修改'
    const api = `${process.env.APIPATH}/api/${
      process.env.COSTOMPATH
    }/products?`
    const vm = this
    console.log(api)
    this.$http.get(api).then(response => {
      vm.products = response.data.products
      vm.products.forEach(item => {
        if (item.id === this.$router.currentRoute.query.id) {
          vm.product = item
        }
      })
      console.log(vm.products)
    })
  }
}
</script>

<style lang="scss" scoped>
$imgbox-color: #bdbdbd;
.imgbox {
  width: 200px;
  height: 200px;
  border: 1px dashed $imgbox-color;
  color: $imgbox-color;
}
a{
  text-decoration: none;
  color: $imgbox-color;
}
.fil-input{
  position: relative;
  input{
    position: absolute;
    font-size: 100px;
    right: 0;
    top: 0;
    // opacity: 0;
    filter: alpha(opacity=0);
    cursor: pointer
  }
}
.hoverEffect{
  position: absolute;
  z-index: 10;
  .iconButton{
    background: transparent;
    border-radius: 50px;
    border: 2px solid rgba(179, 177, 177, 0.788);
    width: 33%;
    height: 33%;
  }
}
.hoverEffectbg{
  background: #00000080;
}
.loadingbg{
  background: white;
}
</style>



