<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
        <button class="btn btn-outline-primary" @click.prevent="gotoCart"><i class="fas fa-shopping-cart mr-2"></i>購物車<span class="ml-2 badge badge-danger" v-if="cartNum > 0">{{cartNum}}</span></button>
    </div>
    <div class="row mt-5">
      <div class="col-md-4 mb-4" v-for="(item,key) in products" :key="key">
        <div class="card border-0 shadow-sm">
          <img class="card-img-top mt-2" :src="item.image" style="height: 250px;">
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{item.title}}</a>
            </h5>
            <p class="card-text">{{item.description}}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <!-- <div class="h5">2,800 元</div> -->
              <del class="h6">原價 {{item.origin_price}} 元</del>
              <div class="h5 text-danger">網路價 {{item.price}} 元</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button type="button" class="btn btn-outline-secondary btn-sm" @click="getProduct(item.id)">
              查看更多
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="productinfor" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalCenterTitle">{{product.title}}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="d-flex justify-content-center">
                        <img :src="product.image" style="width:80%; height:300px;">
                    </div>
                    <div class="mt-4 font-weight-bold h5">{{product.description}}</div>
                    <div class="d-flex justify-content-between align-items-baseline mt-5">
                    <!-- <div class="h5">2,800 元</div> -->
                        <del class="h6">原價 {{product.origin_price}} 元</del>
                        <div class="h5 text-danger">網路價 {{product.price}} 元</div>
                    </div>
                    <select name="" class="form-control mt-3" v-model="product.num" id="qty">
                        <option :value="num" v-for="num in 10" :key="num">
                            選購 {{num}} {{product.unit}}
                        </option>
                    </select>
                </div>
                <div class="modal-footer">
                    <div class="text-muted text-nowrap mr-3">
                        小計 <strong>{{product.num * product.price}}</strong> 元
                    </div>
                    <button type="button" class="btn btn-primary" @click="addtoCart(product.id, product.num)">加到購物車</button>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
      products: [],
      product: {},
      isLoading: false,
      cartNum: 0
    }
  },
  methods: {
    getProducts (page = 1) {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/products?`
      const vm = this
      // console.log(api)
      vm.isLoading = true
      this.$http.get(api).then(response => {
        vm.isLoading = false
        vm.products = response.data.products
        // console.log(vm.products)
      })
    },
    // 取得單一產品
    getProduct (id) {
      console.log(id)
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/product/${id}`
      const vm = this
      vm.isLoading = true
      this.$http.get(api).then(response => {
        vm.isLoading = false
        const temp = response.data.product
        temp.num = 1
        vm.product = temp
        $('#productinfor').modal('show')
      })
    },
    addtoCart (id, qty = 1) {
      console.log(id)
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/cart`
      const cart = {
        product_id: id,
        qty
      }
      const vm = this
      this.$http.post(api, {data: cart}).then(response => {
        console.log(response)
        $('#productinfor').modal('hide')
        vm.getCarts()
      })
    },
    gotoCart () {
      if (this.cartNum <= 0) return
      this.$router.push('/customer_cart')
    },
    getCarts () {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/cart`
      const vm = this
      this.$http.get(api).then(response => {
        console.log(response.data.data.carts.length)
        vm.cartNum = response.data.data.carts.length
      })
    }
  },
  created () {
    this.getProducts()
    this.getCarts()
  }
}
</script>



