<template>
    <div class="py-5">
        <div class="row d-flex justify-content-center">
            <div class="col-8">
                <h3 class="text-center">確認訂單資訊</h3>
                <table class="table mt-5">
                    <thead>
                        <tr>
                            <th scope="col">品名</th>
                            <th scope="col">數量</th>
                            <th scope="col">價格</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item,key) in products" :key="key">
                            <td>{{item.product.title}}</td>
                            <td>{{item.qty}}/{{item.product.unit}}</td>
                            <td class="text-right">{{item.final_total | currency}}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td scope="col" colspan="2" class="text-right">總額</td>
                            <td class="text-right">{{order.total | currency}}</td>
                        </tr>
                    </tfoot>
                </table>
                <table class="table pt-5">
                    <tbody>
                        <tr>
                            <th width="100">Email</th>
                            <td>{{ user.email }}</td>
                        </tr>
                        <tr>
                            <th width="100">姓名</th>
                            <td>{{ user.name }}</td>
                        </tr>
                        <tr>
                            <th width="100">收件人電話</th>
                            <td>{{ user.tel }}</td>
                        </tr>
                        <tr>
                            <th width="100">收件人地址</th>
                            <td>{{ user.address  }}</td>
                        </tr>
                        <tr>
                            <th>付款狀態</th>
                            <td>
                                <span v-if="!order.is_paid">尚未付款</span>
                                <span v-else class="text-success">付款完成</span>
                            </td>
                        </tr>
                    </tbody>
                </table>
                <div class="text-right" v-if="order.is_paid === false">
                    <button class="btn btn-danger" @click="payOrder">確認付款去</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      orderId: '',
      order: {},
      user: {},
      products: []
    }
  },
  methods: {
    getOrder () {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/order/${this.orderId}`
      const vm = this
      // console.log(api)
      this.$http.get(api).then(response => {
        vm.order = response.data.order
        vm.products = response.data.order.products
        vm.user = response.data.order.user
        console.log(response)
      })
    },
    payOrder () {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/pay/${this.orderId}`
      const vm = this
      this.$http.post(api).then(response => {
        console.log(response)
        vm.getOrder()
      })
    }
  },
  created () {
    this.orderId = this.$route.params.orderId
    console.log(this.orderId)
    this.getOrder()
  }
}
</script>



