<template>
  <div>
    <loading :active.sync="isLoading"></loading>
      <div class="text-right mt-4">
          <button class="btn btn-primary" @click.prevent="newProducts(false)"><i class="mr-2 fas fa-plus"></i>新增產品</button>
      </div>
      <table class="table mt-4">
          <thead class="thead-light">
              <tr>
                  <th width="120">分類</th>
                  <th>產品名稱</th>
                  <th class="text-right" width="120">原價</th>
                  <th class="text-right" width="120">售價</th>
                  <th class="text-center" width="100">啟用狀態</th>
                  <th class="text-center" width="100">編輯</th>
              </tr>
          </thead>
          <tbody>
              <tr v-for="(items) in products" :key="items.id">
                  <td>{{items.category}}</td>
                  <td>{{items.title}}</td>
                  <td class="text-right">{{items.origin_price | currency}}</td>
                  <td class="text-right">{{items.price | currency}}</td>
                  <td class="text-center">
                      <span v-if="items.is_enabled" class="text-success">啟用</span>
                      <span v-else class="text-secondary">未啟用</span>
                  </td>
                  <td>
                      <a class="btn btn-link far fa-edit py-0" @click.prevent="newProducts(true,items.id,items)"></a>
                      <a class="btn btn-link far fa-trash-alt py-0 text-danger pl-0" @click.prevent="deleteProduct(items.id)"></a>
                  </td>
              </tr>
          </tbody>
      </table>
      <nav aria-label="Page navigation example">
        <ul class="pagination d-flex justify-content-center pt-4">
          <li class="page-item" :class="{'disabled': !pagination.has_pre}">
            <a class="page-link" href="#" aria-label="Previous" @click.prevent="getProducts(pagination.current_page - 1)">
              <span aria-hidden="true">&laquo;</span>
              <span class="sr-only">Previous</span>
            </a>
          </li>
          <li class="page-item" v-for="page in pagination.total_pages" :key="page" :class="{'active':pagination.current_page === page}">
            <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
          </li>
          <li class="page-item" :class="{'disabled': !pagination.has_next}">
            <a class="page-link" href="#" aria-label="Next" @click.prevent="getProducts(pagination.current_page + 1)">
              <span aria-hidden="true">&raquo;</span>
              <span class="sr-only">Next</span>
            </a>
          </li>
        </ul>
        </nav>
  </div>
</template>

<script>
export default {
  data () {
    return {
      products: [],
      isLoading: false,
      pagination: {}
    }
  },
  methods: {
    getProducts (page = 1) {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/products?page=${page}`
      const vm = this
      // console.log(api)
      vm.isLoading = true
      this.$http.get(api).then(response => {
        vm.isLoading = false
        vm.products = response.data.products
        vm.pagination = response.data.pagination
        // console.log(vm.products)
      })
    },
    newProducts (Mode, key, items) {
      if (!Mode) {
        this.$router.push({path: '/admin/new'})
      } else {
        this.$router.push({path: '/admin/new', query: { id: key }})
        this.$bus.$emit('productinfor:push', items)
      }
    },
    deleteProduct (key) {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/admin/product/${key}`
      this.$http.delete(api).then(response => {
        console.log(response.data)
        this.getProducts()
      })
    }
  },
  created () {
    this.getProducts()
  }
}
</script>



