<template>
  <div>
    <nav class="col-md-2 d-none d-md-block bg-light sidebar">
      <div class="sidebar-sticky">
        <ul class="nav flex-column">
          <li class="nav-item">
            <div class="nav-link text-secondary">管理員</div>
          </li>
          <li class="nav-item">
            <a class="nav-link" href="#" :class="{ 'active' : state==='products' }" @click.prevent="toPage('products')">
              <span data-feather="file"></span>
              <i class="fas fa-box"></i>
              商品列表
            </a>
          </li>
          <li class="nav-item">
            <a class="nav-link" href="#" :class="{ 'active' : state==='orderlist' }" @click.prevent="toPage('orderlist')">
              <span data-feather="shopping-cart"></span>
              <i class="fas fa-clipboard-list"></i>
              訂單列表
            </a>
          </li>
          <li class="nav-item">
            <a class="nav-link" href="#" :class="{ 'active' : state==='coupons' }" @click.prevent="toPage('coupons')">
              <span data-feather="users"></span>
              <i class="fas fa-ticket-alt"></i>
              優惠券
            </a>
          </li>
        </ul>
        <ul class="nav flex-column">
          <li class="nav-item">
            <div class="nav-link text-secondary">模擬功能</div>
          </li>
          <li class="nav-item">
            <a class="nav-link" href="#" @click.prevent="toPage('customer_order')" :class="{ 'active' : state==='customer_order' }">
              <span data-feather="file"></span>
              <i class="fas fa-shopping-cart"></i>
              模擬訂單
            </a>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>

<script>
export default {
  data () {
    return {
      state: 'products'
    }
  },
  methods: {
    toPage (state) {
      const vm = this
      vm.state = state
      const path = '/admin/'
      if (state === 'customer_order') {
        vm.$router.push('/' + state)
      } else {
        vm.$router.push(path + state)
      }
    }
  },
  created () {
    console.log(this.$route.name)
    this.state = this.$route.name
  }
}
</script>



