<template>
    <div>
    <loading :active.sync="isLoading"></loading>
      <div class="text-right mt-4">
          <button class="btn btn-primary" @click.prevent="newCoupon"><i class="mr-2 fas fa-plus"></i>新增優惠券</button>
      </div>
      <table class="table mt-4">
          <thead class="thead-light">
              <tr>
                  <th>名稱</th>
                  <th width="120">折扣百分比</th>
                  <th width="120">到期日</th>
                  <th width="150">優惠券代碼</th>
                  <th width="120" class="text-center">是否啟用</th>
                  <th class="text-center" width="100">編輯</th>
              </tr>
          </thead>
          <tbody>
              <tr v-for="(items) in coupons" :key="items.id">
                  <td>{{items.title}}</td>
                  <td>{{items.percent}}</td>
                  <td>{{items.due_date}}</td>
                  <td>{{items.code}}</td>
                  <td class="text-center">
                      <span v-if="items.is_enabled" class="text-success">啟用</span>
                      <span v-else class="text-secondary">未啟用</span>
                  </td>
                  <td>
                      <a class="btn btn-link far fa-edit py-0" @click.prevent="newCoupon(true,items.id,items)"></a>
                      <a class="btn btn-link far fa-trash-alt py-0 text-danger pl-0" @click.prevent="deleteCoupon(items.id)"></a>
                  </td>
              </tr>
          </tbody>
      </table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      coupons: [],
      isLoading: false
    }
  },
  methods: {
    newCoupon (Mode, key, items) {
      if (!Mode) {
        this.$router.push({path: '/admin/newcoupon'})
      } else {
        this.$router.push({path: '/admin/newcoupon', query: { id: key }})
      }
    },
    getCoupon (page = 1) {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/admin/coupons?page=${page}`
      const vm = this
      // console.log(api)
      vm.isLoading = true
      this.$http.get(api).then(response => {
        console.log(response.data)
        vm.isLoading = false
        vm.coupons = response.data.coupons
        vm.pagination = response.data.pagination
        console.log(vm.coupons)
      })
    },
    deleteCoupon (key) {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/admin/coupon/${key}`
      this.$http.delete(api).then(response => {
        console.log(response.data)
        this.getCoupon()
      })
    }
  },
  created () {
    this.getCoupon()
  }
}
</script>



