<template>
    <div class="py-5">
        <div class="row d-flex justify-content-center">
            <div class="col-8">
                <form>
                    <div class="form-group">
                        <label for="username">收件人</label>
                        <input type="text" class="form-control" id="username"  name="name"
                        placeholder="請輸入收件人" v-model="customerInfor.name" v-validate="'required'"
                        :class="{'is-invalid': errors.has('name')}">
                        <span class="text-danger" v-if="errors.has('name')">收件人不得為空白</span>
                    </div>
                    <div class="form-group">
                        <label for="email">電子郵件</label>
                        <input type="email" class="form-control" id="email" placeholder="請輸入您的Email"
                         v-model="customerInfor.email" v-validate="'required|email'" name="email"
                         :class="{'is-invalid': errors.has('email')}">
                        <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
                    </div>
                    <div class="form-group">
                        <label for="phone">連絡電話</label>
                        <input type="tel" class="form-control" id="phone" placeholder="請輸入您的電話" v-model="customerInfor.tel"
                        name="phone" v-validate="'required'" :class="{'is-invalid': errors.has('phone')}">
                        <span class="text-danger" v-if="errors.has('phone')">連絡電話不得為空白</span>
                    </div>
                    <div class="form-group">
                        <label for="address">收件地址</label>
                        <input type="text" class="form-control" id="address" placeholder="請輸入收件地址" v-model="customerInfor.address"
                        name="address" v-validate="'required'" :class="{'is-invalid': errors.has('address')}">
                        <span class="text-danger" v-if="errors.has('address')">收件地址不得為空白</span>
                    </div>
                    <div class="form-group">
                        <label for="infor">留言</label>
                        <textarea
                        class="form-control"
                        id="infor"
                        rows="5"
                        placeholder="請寫下想說的話"
                        v-model="message"
                        ></textarea>
                    </div>
                    <div class="text-right mt-4">
                        <button type="submit" class="btn btn-primary" @click.prevent="order">提交</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      customerInfor: {},
      message: ''
    }
  },
  methods: {
    order () {
      const api = `${process.env.APIPATH}/api/${
        process.env.COSTOMPATH
      }/order`

      let data = {
        data: {
          user: this.customerInfor
        },
        message: this.message
      }
      const vm = this
      this.$validator.validate().then(result => {
        if (result) {
          this.$http.post(api, data).then(response => {
            if (response.data.success) {
              vm.$router.push(`/customer_checkinfor/${response.data.orderId}`)
            }
            console.log(response.data)
          })
        }
      })
    }
  }
}
</script>



