import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/pages/Login'
import Dashboard from '@/components/DashBoard'
import Products from '@/components/pages/products'
import newProducts from '@/components/pages/newProducts'
import Coupon from '@/components/pages/coupon'
import OrderList from '@/components/pages/orderlist'
import newCoupon from '@/components/pages/newCoupon'
import CustomOrder from '@/components/pages/CustomOrder'
import CustomCart from '@/components/pages/cart'
import CustomCheckout from '@/components/pages/checkout'
import CustomerCheckInfor from '@/components/pages/customerCheckout'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: 'login'
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      children: [
        { // 產品列表
          path: 'products',
          name: 'products',
          component: Products,
          meta: { requiresAuth: true } // 要求驗證
        },
        { // 新增產品
          path: 'new',
          name: 'newproducts',
          component: newProducts,
          meta: { requiresAuth: true } // 要求驗證
        },
        { // 訂單列表
          path: 'orderlist',
          name: 'orderlist',
          component: OrderList,
          meta: { requiresAuth: true } // 要求驗證
        },
        { // 優惠券列表
          path: 'coupons',
          name: 'coupons',
          component: Coupon,
          meta: { requiresAuth: true } // 要求驗證
        },
        { // 新增優惠券
          path: 'newcoupon',
          name: 'newcoupon',
          component: newCoupon,
          meta: { requiresAuth: true } // 要求驗證
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '',
      name: 'Dashboard',
      component: Dashboard,
      children: [
        { // 模擬購物
          path: 'customer_order',
          name: 'customer_order',
          component: CustomOrder
        },
        { // 模擬購物購物車
          path: 'customer_cart',
          name: 'customer_cart',
          component: CustomCart
        },
        { // 模擬購物結帳
          path: 'customer_checkout',
          name: 'customer_checkout',
          component: CustomCheckout
        },
        { // 模擬購物確認結帳資訊
          path: 'customer_checkinfor/:orderId',
          name: 'customer_checkinfor',
          component: CustomerCheckInfor
        }
      ]
    }
  ]
})



